.textarea {
  width: 17.5rem;
  min-height: 6rem;
  border-radius: 0.625rem;
  background: var(--light-gray);
  background-color: (64, 67, 75, 1);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  padding: 0.5rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  resize: none;
}
